:root {
    --background-color: #002248;
    --body-background: #24A9BD;
}

body {
    font-size: 20px;
    font-family: 'Source Sans Pro', 'sans-serif';
    background-image: linear-gradient(white, var(--background-color));
}

* {
    padding: 0px;
    margin: 0px;
    scroll-behavior: smooth;
}

.link {
    text-decoration: none;
    padding: 5px;
    color: white;
    font-size: 2rem;
}

.link:hover {
    color: yellow;
    transition: color 1.5s;
}
.flex {
    display: flex;
}

.center {
    justify-content: center;
    align-items: center;
}

.wrap {
    flex-wrap: wrap;
}

.column {
    flex-direction: column
}

.between {
    justify-content: space-between;
}

.baseline {
    align-items: flex-start;
}

.margin-top {
    margin-top: 5rem;
}

.nav {
    background-color: var(--background-color)
}

.section {
    min-height: 100vh;
    position: relative;
}

.footer {
    background-color: var(--background-color);
}

.icon {
    font-size: 2em;
    color: white;
    padding: 5px;
}

.sign {
    color: white;
    font-size: 1rem;
}

.photo {
    background-repeat: no-repeat;
    background-attachment: fixed;
    height: 50vh;
    background-size: cover;
    position:relative;

}

.offer {
    background-image: url('../assets/offer.jpg');
}

.about {
    background-image: url('../assets/about.jpg')
}

.contact {
    background-image: url('../assets/contact.jpg');
}

.photo-header {
    position: absolute;
    top: 20%;
    height: 100%;
    font-size: 10rem;
    color: white;
}

.mask {
    background-color: var(--background-color);
    height: 100%;
    width: 100%;
    opacity: 0.5;
}

.content {
    padding: 5%;
    height: 100%;
    margin-bottom: 5%;
}

.offer-text {
    align-self: center;
}

@keyframes underline {
    0% {text-decoration: none}
    100% {text-decoration: underline}
}
.offer-text:hover {
    text-decoration: underline;
    animation: underline 2s ease-in-out;
}

.offer-image {
    width: 100%;
    max-width: 400px;
}

.offer-section {
    display: grid;
    grid-template-columns: 50% 50%;
    border-bottom: 1px solid var(--background-color);
    margin-bottom: 3rem;
    margin: 0 10%;
}

.button {
    margin: 10px;
    background-color: transparent;
    color: var(--background-color);
    border: 2px solid var(--background-color);
    padding: 10px;
    font-weight: bold;
    width: 150px;
    height: 50px;
}


.button:hover {
    background-image: none;
    background-color: var(--background-color);
    color: yellow;
    transition: 1s;
    cursor: pointer;
}

.about-section {
    width: 300px;
    height: 50vh;
    align-items: center;
    margin-bottom: 20px;

}

.about-section>* {
    padding-top: 20px;
}



.paragraph {
    text-align: justify;
    border-radius: 10px 10px;
    padding: 10px;
    color: white;
    margin-bottom: 20px;

}

.about-icon {
    color: yellow;
    font-size: 3rem;
}

.section-text {
    color: yellow;
    font-size: 3rem;
    padding-left: 20px;
}

